import * as axios from "axios";
import store from "../redux/store-redux"

const getAPIKEY = () => {
    return store.getState().auth.API_KEY
}

const axiosInstance = axios.create({
    withCredentials: true,
    baseURL: "https://social-network.samuraijs.com/api/1.0",
})

export const usersAPI = {
    getUsers(currentPage = 1, pageSize = 10) {
        return axiosInstance.get(`/users?page=${currentPage}&count=${pageSize}`, {headers: {
                "API-KEY": getAPIKEY()
            }})
            .then(response => {
                return response.data
            })
    },
    getTotalUsersCount() {
        return axiosInstance.get(`/users`)
            .then(response => {
                return response.data
            })
    },
    followToUser(userId) {
        return axiosInstance.post(`/follow/${userId}`, {headers: {
                "API-KEY": getAPIKEY()
            }})
            .then(response => {
                return response.data
            })
    },
    unfollowToUser(userId) {
        return axiosInstance.delete(`/follow/${userId}`, {headers: {
                "API-KEY": getAPIKEY()
            }})
            .then(response => {
                return response.data
            })
    }
}

export const authAPI = {
    authMe() {
        return axiosInstance.get(`/auth/me`)
            .then(response => {
                return response.data
            })
    },
    loginUser(email, password, rememberMe, captcha = null) {
        return axiosInstance.post(`/auth/login`, {email, password, rememberMe, captcha})
    },
    logoutUser() {
        return axiosInstance.post(`/auth/logout`)
            .then(response => {
                return response.data
            })
    }
}

export const profileAPI = {
    getProfile(userId) {
        return axiosInstance.get(`/profile/${userId}`)
            .then(response => {
                return response.data
            })
    },
    getStatus(userId) {
        return axiosInstance.get(`/profile/status/${userId}`)
    },
    updateStatus(status) {
        return axiosInstance.put(`/profile/status`, {status: status}, {headers: {
                "API-KEY": getAPIKEY()
            }})
    },
    savePhoto(photoFile) {
        let formData = new FormData()
        formData.append("image", photoFile)
        return axiosInstance.put(`/profile/photo`, formData, {
            headers: {
                'Content-Type': 'multipart/form-data',
                "API-KEY": getAPIKEY()
            }
        })
            .then(response => {
                return response.data
            })
    },
    saveProfile(profile) {
        return axiosInstance.put(`/profile`, profile, {headers: {
                "API-KEY": getAPIKEY()
            }})
            .then(response => {
                return response.data
            })
    }
}

export const messagesAPI = {
    getDialogs() {
        return axiosInstance.get(`/dialogs`, {headers: {
                "API-KEY": getAPIKEY()
            }})
    },
    startDialog(userId) {
        return axiosInstance.put(`/dialogs/${userId}`, {headers: {
                "API-KEY": getAPIKEY()
            }})
    },
    getMessagesList(userId) {
        return axiosInstance.get(`/dialogs/${userId}/messages`, {headers: {
                "API-KEY": getAPIKEY()
            }})
    },
    sendMessage(userId, body) {
        return axiosInstance.post(`/dialogs/${userId}/messages`, {body}, {headers: {
                "API-KEY": getAPIKEY()
            }})
    },
    deleteMessage(messageId) {
        return axiosInstance.delete(`/dialogs/messages/${messageId}`, {headers: {
                "API-KEY": getAPIKEY()
            }})
    }
}

export const securityAPI = {
    getCaptchaUrl() {
        return axiosInstance.get(`/security/get-captcha-url`)
    }
}
